.App {
  width: 95%;
  margin: 0 auto;
}

.Filters {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: start;
      justify-content: flex-start;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  border: 1px solid #CCC;
  border-top: none;
}

.Favorites {
  -ms-flex: 1 1 50%;
      flex: 1 1 50%;
  font-size: 1rem;
  text-align: left;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 5px;
  background: transparent;
  border: none;
  border-right: 1px solid #CCC;
  border-bottom: 1px solid #CCC;
  cursor: pointer;
  outline: none;
}

@media(min-width: 450px) {
  .App {
    max-width: 400px;
  }
}

@media(min-width: 600px) {
  .App {
    max-width: 550px;
  }
  .Favorites {
    padding: 10px;
  }
}

@media(min-width: 768px) {
  .App {
    max-width: 700px;
  }
}


